.main-container {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
}

.heading {
  text-align: center;
  
}

.center-container {
  margin-top: 20px;
}

.todo-input {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  margin-bottom: 10px;
}


.add-button {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  background-color: #007bff;
  color: #fff;
  border: none;
  cursor: pointer;
}

.add-button:hover {
  background-color: #0056b3;
}

.list-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
  background-color: white;
  margin-bottom: 5px;
}

.list-item span {
  flex: 1;
  margin-left: 10px;
}

.input-container {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.input-box {
  flex: 1;
  padding: 10px;
  font-size: 16px;
  border: 1px solid black;
  border-radius: 4px;
  margin-right: 10px;
}

.add-btn {
  padding: 10px 20px;
  font-size: 16px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.add-btn:hover {
  background-color: #0056b3;
}
body{
  background-color: rgb(199, 198, 198);
}
.del-btn {
  padding: 10px 20px;
  font-size: 16px;
  background-color: #da2436;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.del-btn:hover {
  background-color: #bd2130;
}
